var lazyLoadInstance = new LazyLoad({
    elements_selector: ".lazy"
});

sal();

function promoVideo(x) {
    var videoHero = document.getElementById('heroVideoPromo');
    
    if (x.matches) {
        videoHero.innerHTML = "<source src='./assets/images/hero_video.mp4' type='video/mp4' >";
    } else {
        videoHero.innerHTML = "<source src='./assets/images/hero_video_mob.mp4' type='video/mp4' >";
    }
}

if ($('#heroVideoPromo').length) {
    var x = window.matchMedia("(min-width: 575px)");
    promoVideo(x);
    x.addListener(promoVideo);
}

function openVideo(video) {
    var videoBlock = document.getElementById(video),
        videoTag = document.getElementById(video + 'Video'),
        videoSrc = videoTag.getAttribute('data-video'),
        parentBlock = videoBlock.parentNode;

    parentBlock.classList.add('open');

    videoBlock.style.display = "block";
    videoTag.innerHTML = "<source id='" + video + "Sourse' src='" + videoSrc + "' type='video/mp4' >";
    videoTag.volume = 0.2;
    videoTag.play();
}
function closeVideo(video) {
    var videoBlock = document.getElementById(video),
        videoTag = document.getElementById(video + 'Video'),
        source = document.getElementById(video + 'Sourse'),
        parentBlock = videoBlock.parentNode;

    parentBlock.classList.remove('open');

    videoBlock.style.display = "none";
    videoTag.pause();
    source.setAttribute('src', '');
}

function removeElement(elementId) {
    var element = document.getElementById(elementId);
    element.parentNode.removeChild(element);
}

var slider = tns({
    container: '#productSlider',
    gutter: 32,
    items: 1,
    lazyload: true,
    mouseDrag: true,
    navPosition: 'bottom',
    controlsContainer: '.slider-controls',
    arrows: {
        next: '.next-btn',
        next: '.next-btn'
    },
    responsive: {
        640: {
            items: 2
        },
        900: {
            items: 3
        }
    }
});

function findVideos() {
	var videos = document.querySelectorAll('.videoYoutube');

	for (var i = 0; i < videos.length; i++) {
		setupVideo(videos[i]);
    
	}
}

function setupVideo(video) {
	var link = video.querySelector('.video__link');
	var button = video.querySelector('.video__button');
	var id = video.getAttribute('data-videoID');

	video.addEventListener("click", function() {
		var iframe = createIframe(id);

		link.remove();
		button.remove();
		video.appendChild(iframe);
	});

	link.removeAttribute('href');
	video.classList.add('video--enabled');
}

function createIframe(id) {
	var iframe = document.createElement('iframe');

	iframe.setAttribute('allowfullscreen', '');
	iframe.setAttribute('allow', 'autoplay');
	iframe.setAttribute('src', generateURL(id));
	iframe.classList.add('video__media');

	return iframe;
}

function generateURL(id) {
	var query = '?rel=0&showinfo=0&autoplay=1';

	return 'https://www.youtube.com/embed/' + id + query;
}

findVideos();

window.addEventListener("scroll",function() {
    var bannerHeight = document.getElementById('block1').offsetHeight;
    var nav = document.getElementById('navigation');
    if(window.pageYOffset > bannerHeight){
        nav.style.display = "block"; 
    }
    else if(window.pageYOffset < bannerHeight){
        nav.style.display = "none";
    }
},false);

$(function() {
    
    $(document).on('click', '.js-scenario-tabs a', function(e) {
        e.preventDefault();

        var tab = $(this),
            tabHash = tab.data('panel'),
            panel = $('.js-scenario-panel#' + tabHash);

        if (!tab.hasClass('active')) {
            $('.js-scenario-panel').fadeOut(100).removeClass('active');
            $('.js-scenario-tabs a').removeClass('active');

            setTimeout(function() {
                panel.fadeIn(100).addClass('active');
                tab.addClass('active');
            }, 100);
        }
    });

    var $owlScenarios = $('.js-scenarios-slider'),
        effect = 'fade',
        outIndex,
        isDragged = false;

    $owlScenarios.owlCarousel({
        margin: 0,
        nav: true,
        navText: ['<svg width="8" height="14" viewBox="0 0 8 14" xmlns="http://www.w3.org/2000/svg"><path d="M2.793 7L7.93 1.917 6.497.5-.071 7l6.568 6.5 1.432-1.417z" fill="#181818" fill-rule="nonzero"></path></svg>','<svg width="8" height="14" viewBox="0 0 8 14" xmlns="http://www.w3.org/2000/svg"><path d="M5.207 7L.07 1.917 1.503.5 8.071 7l-6.568 6.5-1.432-1.417z" fill="#181818" fill-rule="nonzero"></path></svg>'],
        items: 1,
        loop: false,
        autoplay: true,
        autoplayHoverPause: true,
        autoplayTimeout: 5000,
        mouseDrag: false,
        touchDrag: true,
        pullDrag: false,
        lazyLoad:true
    });

    $owlScenarios.on('changed.owl.carousel', function(event) {
        var current = event.item.index;
        var currentVideo = $(event.target).find('.owl-item').eq(current).find('video');
        var currentVideoSource = currentVideo.find('source');
      
        // Pause all videos
        var videos = $owlScenarios.find('video');
      
        if ( videos.length ) {
          videos.each(function() {
            $(this)[0].pause();
          });
        }
      
        // Play if video found
        if ( currentVideo.length ) {
          var currentVideoSrc = currentVideoSource.attr('data-src');
          currentVideoSource.attr('src', currentVideoSrc);
          currentVideo[0].load();
          currentVideo[0].play();
        }
      });


    $('.js-scenario-video').each(function(e) {
        var video = $(this),
            parent = $(this).closest('.js-scenario-media'),
            replayBlock = parent.find('.js-replay-block'),
            replayBtn = replayBlock.find('.js-scenario-replay');

        video.on('ended',function(){
            replayBlock.fadeIn(100);
        });

        replayBtn.click(function(e) {

            e.preventDefault();
    
            video.get(0).play();
            replayBlock.fadeOut(100);
    
        });
        
    });

    var $owlScenarios = $('.js-scenarios-slider'),
        owlOptions = {
            margin: 0,
            nav: true,
            navText: ['<svg width="8" height="14" viewBox="0 0 8 14" xmlns="http://www.w3.org/2000/svg"><path d="M2.793 7L7.93 1.917 6.497.5-.071 7l6.568 6.5 1.432-1.417z" fill="#181818" fill-rule="nonzero"></path></svg>','<svg width="8" height="14" viewBox="0 0 8 14" xmlns="http://www.w3.org/2000/svg"><path d="M5.207 7L.07 1.917 1.503.5 8.071 7l-6.568 6.5-1.432-1.417z" fill="#181818" fill-rule="nonzero"></path></svg>'],
            items: 1,
            loop: false,
            autoplay: true,
            autoplayHoverPause: true,
            autoplayTimeout: 5000,
            mouseDrag: false,
            touchDrag: true,
            pullDrag: false,
            lazyLoad:true,
        };

    if ( $(window).width() < 768 ) {
        var owlActive = $owlScenarios.owlCarousel(owlOptions);
        $owlScenarios.on('changed.owl.carousel', function(event) {
            var current = event.item.index;
            var currentVideo = $(event.target).find('.owl-item').eq(current).find('video');
            var videoSrc = currentVideo.data('video');
            $('.js-replay-block').fadeOut(100);
          
            // Play if video found
            if ( currentVideo.length ) {
                currentVideo.html("<source src='" + videoSrc + "' type='video/mp4' >");
            }
          });
    } else {
        $owlScenarios.addClass('off');
    }

    $(window).resize(function() {
        if ( $(window).width() < 768 ) {
            if ( $('.js-scenarios-slider').hasClass('off') ) {
                var owlActive = $owlScenarios.owlCarousel(owlOptions);
                $owlScenarios.removeClass('off');
                $owlScenarios.on('changed.owl.carousel', function(event) {
                    var current = event.item.index;
                    var currentVideo = $(event.target).find('.owl-item').eq(current).find('video');
                    var videoSrc = currentVideo.data('video');
                    $('.js-replay-block').fadeOut(100);
                
                    // Play if video found
                    if ( currentVideo.length ) {
                        currentVideo.html("<source src='" + videoSrc + "' type='video/mp4' >");
                    }
                });
            }
        } else {
            if ( !$('.js-scenarios-slider').hasClass('off') ) {
                $owlScenarios.addClass('off').trigger('destroy.owl.carousel');
                $owlScenarios.find('.owl-stage-outer').children(':eq(0)').unwrap();
            }
        }
    });
});